import React from 'react';

import './Work.css';

function WorkCard(props) {

    function handleDelete(event) {
        console.log(event);
        event.target.parentElement.remove();
    }

    return (
        <div className='workCard'>
            <div className='workDel' onClick={handleDelete}>X</div>
            <div className='workDisplay'>
                <div className='workInfo'>
                    <div className='info'>{props.company}</div>
                    <div className='info'>{props.title}</div>
                    <div className='info'>{props.start}</div>
                    <div className='info'>{props.end}</div>
                </div>
                <div className='descDisp'>
                    <div className='desc'>{props.desc}</div>
                </div>
            </div>
        </div>
    )
};

export default WorkCard