import React, {useState} from 'react';

import './Education.css';

function EducationForm(props) {

    let [schoolName, setSchoolName] = useState('');
    let [degree, setDegree] = useState('');
    let [gradYear, setGradYear] = useState('');
    let [city, setCity] = useState('');

    function handleSchoolChange(event) {
        setSchoolName(event.target.value);
    };

    function handleDegreeChange(event) {
        setDegree(event.target.value);
    };

    function handleGradChange(event) {
        setGradYear(event.target.value);
    };

    function handleCityChange(event) {
        setCity(event.target.value);
    }

    function handleSubmit(event) {
        event.preventDefault();
        let userEdu = {
            schoolName: schoolName,
            degree: degree,
            gradYear: gradYear,
            city: city
        }
        props.onSave(userEdu);
    }

    return (
        <form className='eduForm' onSubmit={handleSubmit}>
            <div className='eduIn'>
                <label> School Name
                    <input
                        className='textIn'
                        onChange={handleSchoolChange}
                        type='text'
                        required>
                    </input>
                </label>
                <label> Degree Obtained
                    <input
                        className='textIn'
                        onChange={handleDegreeChange}
                        type='text'>
                    </input>
                </label>
                <label> Graduation Year
                    <input
                        className='textIn'
                        onChange={handleGradChange}
                        type='text'>
                    </input>
                </label>
                <label> City
                    <input
                        className='textIn'
                        onChange={handleCityChange}
                        type='city'>
                    </input>
                </label>
            </div>
            <button onClick={props.onSubmit} className='add' type='submit'>Submit</button>
            <button onClick={props.onCancel} className='add' type='button'>Cancel</button>
        </form>
    )
};

export default EducationForm