import React, {useState} from "react";

import './Work.css';

import WorkForm from './WorkForm';
import WorkCard from './WorkCard';

function Work() {

    let [workExp, setWorkExp] = useState([]);
    let [isEditing, setIsEditing] = useState(false);

    function newDataHandler(info) {
        setWorkExp(prevExp => {
            return [info, ...prevExp];
        });
        setIsEditing(false);
    };

    function handleAddClick() {
        setIsEditing(true);
    };

    function handleCancel() {
        setIsEditing(false);
    };

    function handleEdit() {
        setIsEditing(true);
    };

    console.log(workExp);

    return (
        <div className='workContainer'>
            <div className='workTitle'>Work Experience</div>
            {workExp.map(exp => <WorkCard key={exp.title} onEdit={handleEdit} company={exp.company} title={exp.title} start={exp.start} end={exp.ed} desc={exp.desc} />)}
            {isEditing && <WorkForm onCancel={handleCancel} onNewData={newDataHandler} />}
            {!isEditing && <button className='add' onClick={handleAddClick}>Add</button>}
        </div>
    );
};

export default Work

