import React from 'react';

import './Card.css';

function Card(props) {

    return (
        <div className='card'>
            <div className='cardInfo'>
                <div className='name'>{props.userInfo.name}</div>
                <div className='info'>{props.userInfo.phone}</div>
                <div className='info'>{props.userInfo.email}</div>
            </div>
            <div className='cardDisp'>
                <div className='image'>Picture</div>
                <button className='edit' onClick={props.onEdit}>Edit Info</button>
            </div>
        </div>
    );
};

export default Card