import React, { useState } from 'react';

import './Form.css'

function Form(props) {
    // State
    let [firstName, setFirstName] = useState(props.userInfo.firstName);
    let [lastName, setLastName] = useState(props.userInfo.lastName);
    let [phone, setPhone] = useState(props.userInfo.phone);
    let [email, setEmail] = useState(props.userInfo.email);


    // Handlers
    function handleFirstNameChange(event) {
        setFirstName(event.target.value);
    }

    function handleLastNameChange(event) {
        setLastName(event.target.value);
    }

    function handlePhoneChange(event) {
        setPhone(event.target.value)
    };

    function handleEmailChange(event) {
        setEmail(event.target.value)
    };

    function handleSubmit(event) {
        event.preventDefault();
        let info = {
            firstName: firstName,
            lastName: lastName,
            phone: phone,
            email: email,
            name: firstName + ' ' + lastName
        };
        props.onSaveInfo(info);
    };

    
    return (
        <form onSubmit={handleSubmit}>
            <div className='formContainer'>
                <label> First Name
                    <input
                        onChange={handleFirstNameChange}
                        className='textIn'
                        type='text'
                        value={firstName}
                        required>
                    </input>
                </label>
                <label> Last Name
                    <input
                        onChange={handleLastNameChange}
                        className='textIn'
                        type='text'
                        value={lastName}>
                    </input>
                </label>
                <label> Phone
                    <input
                        className='textIn'
                        type='text'
                        onChange={handlePhoneChange}
                        value={phone}>
                    </input>
                </label>
                <label> Email
                    <input
                        onChange={handleEmailChange}
                        className='textIn'
                        type='text'
                        value={email}>
                    </input>
                </label>
            </div>
            <button className='formSubmit' type='submit'>Save</button>
        </form>
    )
}

export default Form