import React from 'react';

import './Education.css';

function EducationCard(props) {

    function handleDelete(event) {
        console.log(event.target.parentElement);
        event.target.parentElement.remove();
    }

    return (
        <div className='eduCard'>
            <div className='eduCardInfo' key={props.degree}>
                <div className='eduInfo'>{props.schoolName}</div>
                <div className='eduInfo'>{props.degree}</div>
                <div className='eduInfo'>{props.gradYear}</div>
                <div className='eduInfo'>{props.city}</div>
                <button onClick={handleDelete} className='eduDelete'>X</button>
            </div>
        </div>
    );
};

export default EducationCard