import React, { useState } from 'react';

import './Work.css';

function WorkForm(props) {

    let [company, setCompany] = useState('');
    let [title, setTitle] = useState('');
    let [start, setStart] = useState('');
    let [end, setEnd] = useState('');
    let [desc, setDesc] = useState('');

    function handleCompanyChange(event) {
        setCompany(event.target.value);
    };

    function handleTitleChange(event) {
        setTitle(event.target.value);
    };

    function handleStartChange(event) {
        setStart(event.target.value);
    };

    function handleEndChange(event) {
        setEnd(event.target.value);
    };

    function handleDescripChange(event) {
        setDesc(event.target.value);
    };

    function handleSubmit(event) {
        event.preventDefault();
        let workInfo = {
            company: company,
            title: title,
            start: start,
            end: end,
            desc: desc
        };
        props.onNewData(workInfo);
    }

    return (
        <form className='eduForm' onSubmit={handleSubmit}>
            <div className='workIn'>
                <div className='userWork'>
                    <label> Company
                        <input
                            className='textIn'
                            onChange={handleCompanyChange}
                            type='text'
                            required>
                        </input>
                    </label>
                    <label> Title
                        <input
                            className='textIn'
                            onChange={handleTitleChange}
                            type='text'>
                        </input>
                    </label>
                    <label> Start Date
                        <input
                            className='textIn'
                            onChange={handleStartChange}
                            type='text'>
                        </input>
                    </label>
                    <label> End Date
                        <input
                            className='textIn'
                            onChange={handleEndChange}
                            type='text'>
                        </input>
                    </label>
                </div>
                <div className='workDes'>
                    <label> Description
                        <textarea
                            className='descripIn'
                            cols='25'
                            rows='13'
                            onChange={handleDescripChange}
                            type='text'>
                        </textarea>
                    </label>
                </div>
            </div>
            <button className='add' type='submit'>Submit</button>
            <button className='add' type='button' onClick={props.onCancel}>Cancel</button>
        </form>
    )
};

export default WorkForm