import React, {useState} from 'react';

import './Education.css';

import EducationForm from './EducationForm';
import EducationCard from './EducationCard';

function Education() {

    let [isEditing, setIsEditing] = useState(false);
    let [eduItems, setEduItems] = useState([{schoolName: 'Univeristy of Houston',
                                            degree: 'M.S. Thuganomics',
                                            gradYear: '2021',
                                            city: 'Houston'}]);

    function handleClick() {
        setIsEditing(true);
    };

    function handleCancel() {
        setIsEditing(false);
    }

    function handleNewData(info) {
        let userEdu = {
            schoolName: info.schoolName,
            degree: info.degree,
            gradYear: info.gradYear,
            city: info.city
        }
        setEduItems(prevEduItems => {
            return [userEdu, ...prevEduItems];
        });
        setIsEditing(false);
    };

    return (
        <div className='test'>
            <div className='edTitle'>Education</div>
            {eduItems.map(eduItem => <EducationCard key={eduItem.degree} schoolName={eduItem.schoolName} degree={eduItem.degree} gradYear={eduItem.gradYear} city={eduItem.city} />)}
            {isEditing && <EducationForm onCancel={handleCancel} onSave={handleNewData}/>}
            {!isEditing && <button className='add' onClick={handleClick}>Add</button>}
        </div>
    )
};

export default Education

